.layout {
  height: 100vh;
}

.c-project-layout {
  cursor: pointer;
  position: relative;
}

.c-project-layout.empty {
  border: 1px dashed darkgray;
  border-radius: 6px;
}

.c-project-layout.empty img {
  opacity: .3;
}

.c-project-layout img {
  max-width: 100%;
}

.c-project-layout-arrow-wrapper {
  position: absolute;
}

.c-project-layout-arrow-wrapper:hover .show-on-arrow-hover {
  display: block;
}

.c-project-layout-arrow {
  position: absolute;
}

.c-project-layout-arrow:before {
  content: " ";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 15px 40px 15px;
  border-color: transparent transparent #000 transparent;
  position: absolute;
  transform: translate(-50%, -50%);
}

.c-project-layout-arrow.active:before,
.c-project-layout-arrow:hover:before {
  border-color: transparent transparent #333333 transparent;
}

.c-project-layout-arrow-controller {
  position: absolute;
  padding: 5px;
  top: 25px;
  width: 200px;
  background: #fff;
  transform: translateX(-50%);
}

.c-project-layout-arrow-image {
  top: 20px !important;
  width: 250px;
  padding: 0;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 1px 8px rgba(0, 0, 0, 0.2);
}

.c-project-layout-arrow-image img {
  border: 5px solid #fff;
}

.c-project-layout-arrow-image:before {
  content: " ";
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 15px 15px 15px;
  border-color: transparent transparent #ffffff transparent;
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translateY(-100%);
}

.product-select-item {
  padding: 15px;
  border: 1px solid lightgray;
}




