.avatar-uploader,
.avatar-uploader-trigger,
.avatar {
  width: 150px;
  height: 150px;
}

.avatar-uploader {
  display: block;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
}

.avatar-uploader-trigger {
  display: table-cell;
  vertical-align: middle;
  font-size: 28px;
  color: #999;
}

.file-select-modal-wrapper .ant-modal-body {
  max-height: 1000px;
  min-height: 600px;
  overflow: auto;
}

.avatar-select-container {
  width: 100%;
  display: block;
  border-radius: 6px;
}

.avatar-select-holder {
  width: 150px;
  height: 150px;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}

.file-list-item {
  padding-bottom: 100%;
  border: 2px solid white;
  position: relative;
  margin-bottom: 6px;
  background: #f3f3f3;
  -webkit-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.file-list-item.checked {
  border-color: blue;
}

.file-list-item-image {
  height: 100%;
  position: absolute;
  width: auto;
  left: 50%;
  transform: translateX(-50%);
}

.file-list-item-wrapper .i-actions {
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
}

