  display: block;
}

.file-list-recent {
  margin-bottom: 15px;
  padding-bottom: 15px;
}

.avatar-select-holder {
  overflow: hidden;
  position: relative;
}

.avatar-select-image {
  height: 100%;
  width: auto;
  position: absolute;
}

.picture-wall-container {
  -ms-box-orient: horizontal;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
}

.picture-wall-select {
  border: 1px dashed #d9d9d9;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}

.picture-wall-item {
  width: 150px;
  height: 150px;
  margin: 0 5px 5px 0;
  border-radius: 6px;
  position: relative;
  overflow: hidden;
}

.picture-wall-item-image {
  height: 100%;
  left: 50%;
  width: auto;
  position: absolute;
  transform: translateX(-50%);
}

.image-wrap {
  background: #eee;
}




